/* Estilos para los elementos */
.profesor {
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.modulo {
  background-color: #f0f0f0;
  border: 1px solid #aaa;
  border-radius: 3px;
  padding: 5px;
  margin-bottom: 5px;
  cursor: move;
}

/* Estilos para la zona de drop */
.dropzone {
  background-color: #e0e0e0;
  border: 2px dashed #999;
  border-radius: 5px;
  padding: 20px;
  margin-top: 20px;
}

.dropped {
  opacity: 0.5;
}
